import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { Container, Header, SignupPage } from '../styles/Loginstyled';
import LoginForm from '../components/Login/LoginForm';

function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const navigate = useNavigate();

  const handleLogin = () => {
    if (email && password) {
      const storedUsers = JSON.parse(localStorage.getItem('users') || '[]');
      const user = storedUsers.find((u: { email: string; password: string; }) => u.email === email && u.password === password);

      if (user) {
        navigate('/Signup');
      } else {
        alert('   .');
      }
    } else {
      alert('  .');
    }
  };

  return (
    <Container>
      <Header>  <br /></Header>
      <LoginForm
        email={email}
        password={password}
        setEmail={(value: string) => setEmail(value)} //   
        setPassword={(value: string) => setPassword(value)} //   
        handleLogin={handleLogin}
      />
      <SignupPage>  ? <Link to="/Signup"></Link></SignupPage>
    </Container>
  );
}

export default LoginPage;
