import React from 'react';
import { Input, Label, ErrorMessage } from '../../styles/Signupstyled';

type UserFormProps = {
  user: {
    email: string;
    password: string;
    confirmPassword: string;
    username: string;
  };
  handleInputChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  isEmailValid: boolean;
  isPasswordValid: boolean;
  isPasswordsMatching: boolean;
};

const UserForm: React.FC<UserFormProps> = ({ user, handleInputChange, isEmailValid, isPasswordValid, isPasswordsMatching }) => {
  return (
    <div>
      <div>
        <Label htmlFor="email"></Label>
        <Input
          type="email"
          name="email"
          id="email"
          value={user.email}
          onChange={handleInputChange}
        />
        {!isEmailValid && (
          <ErrorMessage>  .</ErrorMessage>
        )}
      </div>
      <div>
        <Label htmlFor="password"></Label>
        <Input
          type="password"
          name="password"
          id="password"
          value={user.password}
          onChange={handleInputChange}
        />
        {!isPasswordValid && (
          <ErrorMessage> 8,    .</ErrorMessage>
        )}
      </div>
      <div>
        <Label htmlFor="confirmPassword"> </Label>
        <Input
          type="password"
          name="confirmPassword"
          id="confirmPassword"
          value={user.confirmPassword}
          onChange={handleInputChange}
        />
        {!isPasswordsMatching && (
          <ErrorMessage>    .</ErrorMessage>
        )}
      </div>
      <div>
        <Label htmlFor="username"></Label>
        <Input
          type="text"
          name="username"
          id="username"
          value={user.username}
          onChange={handleInputChange}
        />
      </div>
    </div>
  );
}

export default UserForm;
